.App {
  height: 100vh;
  display: flex;
  color: white;
}

.App__Aside {
  width: 50%;
  background: url('Slot2.jpg');
  background-size: 683px 683px;
   background-repeat: no-repeat;
  /*background-color:   #FFDF00;*/
  /*background-color: #66DAC7;*/
  padding: 25px 10px;
  overflow: auto;
}
 
.App__Form {
  width: 50%;
  background-color: #2E4158;
   padding: 25px 40px;
   overflow: auto;
   background-image:url('rulet2.png');
   background-size:150%;
    background-repeat: no-repeat;
  }

.PageSwitcher {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 10%;
}

.PageSwitcher__Item {
  background-color: #4C5D72;
  color: #9DA6B1;
  padding: 10px 25px;
  cursor: pointer;
  font-size: .9em;
  border: none;
  outline: none;
  display: inline-block;
  text-decoration: none;
}

.PageSwitcher__Item--Active {
  
  background-color:#e10a0a;
  /*background-color: #5ED0C0;*/
  color: white;
  padding: 10px 25px;
  font-size: .9em;
  border: none;
  outline: none;
  display: inline-block;
  text-decoration: none;
}

.PageSwitcher__Item:first-child {
  border-top-left-radius: 25px;
  border-bottom-left-radius: 25px;
}
.PageSwitcher__Item:last-child {
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;
}

.FormCenter {
  margin-bottom: 100px;
}

.FormField {
  margin-bottom: 40px;
}
.FormField_Asside {
  margin-top:100px;
  }

.FormField__Label {
  display: block;
  text-transform: uppercase;
  font-size: .9em;
  font-weight: bold;
  color: white;
}

.FormField__Input {
  width: 85%;
  background-color: transparent;
  border: none;
  color: white;
   outline: none;
  border-bottom: 1px solid #445366;
  font-size: 1em;
  font-weight: 300;
  padding-bottom: 10px;
  margin-top: 10px;
}

.FormField__Input::placeholder {
  color: white;
  font-weight: bold;
   /*color: #616E7F */
}

.FormField__Button {
  background-color:#e10a0a;
  /*background-color: #52C4B9;*/
  color: white;
  border: none;
  outline: none;
  border-radius: 25px;
  padding: 15px 70px;
  font-size: .8em;
  font-weight: bold;
}

.FormField__Link {
  color: #66707D;
  text-decoration: none;
  display: inline-block;
  border-bottom: 1.5px solid #225E62;
  padding-bottom: 5px;
}

.FormField__CheckboxLabel {
  color: #646F7D;
  font-size: .9em;
}

.FormField__Checkbox {
  position: relative;
  top: 1.5px;
}

.FormField__TermsLink {
  color: white;
  border-bottom: 1px solid #199087;
  text-decoration: none;
  display: inline-block;
  padding-bottom: 2px;
  margin-left: 5px;
}

input.error {
  border: 1px solid blue;
}
.errorMessage {
  color: #dd0000;
  font-size: 0.75em;
  display: flex;
}